.contact {
    margin-top: 10px;
    min-width: 380px;
    padding: 10px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    background-color: rgb(229, 223, 236);
    align-items: center;
    font-size: 20px;
    overflow: auto;
  }
  
  .btnDelete {
    width: 57px;
    height: 25px;
    border: none;
    border-radius: 5px;
    font-size: 14px;
    font-weight: 400;
    cursor: pointer;
    color: blueviolet;
    background-color: white;
    transition: color 100ms cubic-bezier(0.4, 0, 0.2, 1),
  font-size 100ms cubic-bezier(0.4, 0, 0.2, 1);
  }
 
  .btnDelete:hover,
  .btnDelete:focus {
        outline: 2px solid yellow;
        font-size: 16px;
  }